










import { Component, Prop, Vue } from "vue-property-decorator";

const within = (n: number, min: number, max: number) => n >= min && n < max;

@Component({})
export default class WriteBook extends Vue {
  @Prop(String)
  readonly text!: string;

  get page() {
    const page = [];
    let i = 0;
    while (i < this.text.length) {
      const row = [];
      while (this.text[i] === " " && i < this.text.length) ++i;
      if (i >= this.text.length) break;

      let j = 0,
        k = 0;
      while (k < this.glyphPerRow && i + k < this.text.length) {
        if (this.text[i + j] == "\n") {
          j += 1;
          break;
        } else if (
          within(this.text.charCodeAt(i + j), 32, 256) &&
          within(this.text.charCodeAt(i + j + 1), 32, 256)
        ) {
          row.push(this.text[i + j] + this.text[i + j + 1]);
          j += 2;
        } else {
          row.push(this.text[i + j]);
          j += 1;
        }
        ++k;
      }
      if (row.length > 0) {
        while (k < this.glyphPerRow) {
          row.push("");
          ++k;
        }
        page.push(row);
      }
      i += j;
    }
    return page;
  }
  get glyphPerRow() {
    return Math.max(
      Math.ceil(Math.sqrt(this.text.length / 0.7) + 1) +
        Math.ceil((this.text.match(/\n/g) || []).length * 0.35),
      10
    );
  }
}
